.text-danger{color:#af2430 !important}
a.text-danger:hover,a.text-danger:focus{color:#70171f !important}
.text-light{color:#C5C5C5 !important}
a.text-light:hover,a.text-light:focus{color:#9f9f9f !important}
.text-dark{color:#2D2D2D !important}
a.text-dark:hover,a.text-dark:focus{color:#070707 !important}
.text-body{color:#C5C5C5 !important}
.text-muted{color:#C5C5C5 !important}
.text-black-50{color:rgba(0,0,0,0.5) !important}
.text-white-50{color:rgba(255,255,255,0.5) !important}
.text-hide{font:0/0 a;color:transparent;text-shadow:none;background-color:transparent;border:0}
.text-decoration-none{text-decoration:none !important}
.text-break{word-break:break-word !important;overflow-wrap:break-word !important}
.text-reset{color:inherit !important}
.visible{visibility:visible !important}
.invisible{visibility:hidden !important}
.svg-icon{display:inline-block;width:16px;height:16px;color:inherit;vertical-align:middle;fill:none;stroke:currentColor}
.svg-fill{fill:currentColor;stroke:none}
.svg-up{-webkit-transform:rotate(0deg);transform:rotate(0deg)}
.svg-right{-webkit-transform:rotate(90deg);transform:rotate(90deg)}
.svg-down{-webkit-transform:rotate(180deg);transform:rotate(180deg)}
.svg-left{-webkit-transform:rotate(-90deg);transform:rotate(-90deg)}
.slide-enter-active,.slide-leave-active{-webkit-transition:all 300ms ease-in-out;transition:all 300ms ease-in-out}
.slide-enter-to,.slide-leave{max-height:600px;overflow:hidden}
.slide-enter,.slide-leave-to{max-height:0;overflow:hidden}
